import { createStore } from "redux";

const initData = {
  taskList: []
};

export function reducer(state = initData, action) {
  switch (action.type) {
    case "ADD":
      return addReduce(state, action);
    case "DELETE":
      return delReduce(state, action);
    default:
      return state;
  }
}

function addReduce(state, action) {
  let addData = {
    task: action.task,
    limit: action.limit.toLocaleDateString()
  };

  let taskList = state.taskList.slice();
  taskList.unshift(addData);
  return {
    taskList: taskList
  };
}

function delReduce(state, action) {
  let taskList = state.taskList.slice();
  taskList.splice(action.index, 1);

  return {
    taskList: taskList
  };
}

export function addTask(task, limit) {
  return {
    type: "ADD",
    task: task,
    limit: limit
  };
}

export function delTask(index) {
  return {
    type: "DELETE",
    index: index
  };
}

export default createStore(reducer);
