import React, { Component } from "react";
import { connect } from "react-redux";
import { delTask } from "./Store";
// import Button from "@material-ui/core/Button";
import { DeleteForever } from "@material-ui/icons";

class DelForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      delIndex: ""
    };
    this.doAction = this.doAction.bind(this);
  }

  doAction(e) {
    e.preventDefault();
    let action = delTask(this.props.index);
    this.props.dispatch(action);
    this.setState({
      delIndex: ""
    });
  }

  render() {
    return (
      <div>
        <div onClick={this.doAction}>
          <DeleteForever variant="contained" color="action" size="small" />
        </div>
      </div>
    );
  }
}

export default connect(state => state)(DelForm);
